import Fingerprint2 from "./fingerprint";

var deviceRegular = {
    "Nexus": "Nexus One|Nexus S|Galaxy.*Nexus|Android.*Nexus.*Mobile|Nexus 4|Nexus 5|Nexus 6",
    "meizu": "meizu|meizu-m2|m2 note",
    "xiaomi": "xiaomi"
};

var getDeviceId = function () {
    let ua = navigator.userAgent.toLowerCase();
    let result = {};
    if (/(iphone|ipad|ipod|ios)/i.test(ua)) {
        result.type = 'ios';
        result.device = 'ios';// ios暂不用区分手机型号
        return result
    }
    else if (/android/i.test(ua)) {
        result.type = 'android';
        for (let i in deviceRegular) {
            let match = new RegExp(deviceRegular[i], "ig").test(ua);
            if (match) {
                result.device = i;
                return result
            }
        }
        result.device = 'android';      // 默认显示android
        return result
    }
    else {
        // 检测不出ua，默认为android
        result.type = 'android';
        result.device = 'android';
        return result
    }
}

var getFingerPrint = (function () {
    // var options = { excludeUserAgent: true, excludeAvailableScreenResolution: true, excludeJsFonts: true, excludeAddBehavior: true, excludeAdBlock: true };
    var options = { excludeUserAgent: true, excludeAvailableScreenResolution: true };
    var did = getDeviceId();
    var fingerPrint = null;
    var details = [];
    var deleteFp = [];
    if (did.type === 'ios') {
    } else {
        // android
        switch (did.device) {
            case 'meizu':
                options.excludeOpenDatabase = true;
                // options.excludeWebGL  = true;           // sinanews,uc 没有WebGL， 微信有
                // options.excludeCanvas = true;           // 去掉其实不太好 微信的不一致的太多            
                // options.excludeHasLiedLanguages = true; // 微信:true, 其他false
                break;
            case 'xiaomi':
                options.excludeOpenDatabase = true;
                break;
            case 'Nexus':
                options.excludeWebGL = true;
                options.excludeCanvas = true;
                break;
            default:
                options.excludeOpenDatabase = true;
        }
    }
    var initFunc = function (type, fn) {
        var cbFn;
        if (typeof fn === 'function') {
            cbFn = fn;
        }
        if (type === 'all') {
            new Fingerprint2().get(function (result, components) {
                fingerPrint = result;
                details = components;
                cbFn && cbFn({
                    fp: fingerPrint,
                    details: details,
                    deleteFp: deleteFp
                })
            });
        } else {
            for (var i in options) {
                deleteFp.push(i.replace("exclude", ""));
            }
            new Fingerprint2(options).get(function (result, components) {
                fingerPrint = result;
                details = components;
                cbFn && cbFn({
                    fp: fingerPrint,
                    details: details,
                    deleteFp: deleteFp
                })
            });
        }
    }

    return {
        init: function (type, fn) {
            try {
                var init = 0;
                if (document.body) {
                    initFunc(type, fn);
                    init = 1;
                } else {
                    if (!init) {
                        document.addEventListener("DOMContentLoaded", function (event) {
                            initFunc(type, fn)
                        });
                        init = 1;
                    }
                }
            } catch (err) {
                console.log(err);
            }
        },
        get: function () {
            return {
                fp: fingerPrint,
                details: details,
                deleteFp: deleteFp
            }
        }
    }
})()

export default getFingerPrint


