let ua =window.navigator.userAgent.toLowerCase();
let os = /iphone|ipad|ipod/.test(ua) ? "ios" :"android";
let keyArr =["a","b","c","d","e","f","g","h","i","j","k","l","m","n","o","p","q","r","s","t","u","v","w","x","y","z"];
let n = 0
let _et = {};
let prtl = 'https:' == window.location.protocol ? 'https://': 'http://';
function getUid(fn){
    if(window.checkLogin()){
        window.getUserInfo(function(re){
            fn && fn(re.uid || '');
        })
        
    }
    else{
        fn && fn('');
    }
    
}
function getfp(){
    return window.getCookie('fingerprint')||'';
    
}
function getustat(){
    return window.getCookie('ustat')||'';
    
}

function createRequest (url) {
    let img = new Image();
    img.src = url;
}


function filter(pUrl)
{
    return pUrl.indexOf('#')==-1?pUrl:pUrl.split('#')[0];
}
if (typeof Object.assign != 'function') {
  Object.assign = function(target, varArgs) { // .length of function is 2
  
    if (target == null) { // TypeError if undefined or null
      throw new TypeError('Cannot convert undefined or null to object');
    }

    let to = Object(target);

    for (let index = 1; index < arguments.length; index++) {
      let nextSource = arguments[index];

      if (nextSource != null) { // Skip over if undefined or null
        for (let nextKey in nextSource) {
          // Avoid bugs when hasOwnProperty is shadowed
          if (Object.prototype.hasOwnProperty.call(nextSource, nextKey)) {
            to[nextKey] = nextSource[nextKey];
          }
        }
      }
    }
    return to;
  };
}


function getEP(option){

    let channel = window.location.href.match(/\w+\.sina\.cn/)||"";
    channel = channel ?  channel[0].split('.')[0] : "";
    _et[option.ek]?_et[option.ek]++:_et[option.ek]=1;
    
    return [
        keyArr[n++]+"="+ new Date().getTime(), //timestamp
        keyArr[n++]+"="+  encodeURIComponent(option.method), //method
        keyArr[n++]+"="+  (channel?("wap_"+channel):""),  //channel
        keyArr[n++]+"="+  encodeURIComponent(option.ek),  //ek
        keyArr[n++]+"="+  encodeURIComponent(option.src||filter(window.location.href)|| ""),
        keyArr[n++]+"="+ _et[option.ek],  //et
        keyArr[n++]+"="+ encodeURIComponent(JSON.stringify(option.data))    //attribute
    ].join('&')
} 
function getCP(uid){

    return [
        keyArr[n++]+"="+encodeURIComponent(getustat()),    //ustat              
        keyArr[n++]+"="+ os,   
        keyArr[n++]+"="+ uid,   //uid
        keyArr[n++]+"="+ encodeURIComponent(ua)  //
    ].join('&')
}    
function getData(option){
    var arr = [];
    for (let i in option.data[0]){
        arr.push(keyArr[n++] +"="+ option.data[0][i]);
    }
    
    return arr.join('&');
} 

const sinaLog = function(option){
    
    if(!option || !option.method || !option.ek||!option.data||option.data.length==0){
        return {
            msg: '参数不足'
        };
    }
    

    getUid(function(uid){
        n = 0;
        
        let str= [
            keyArr[n++]+"=error",
            keyArr[n++]+"="+option.pk||'', //_pk
            keyArr[n++]+"=web",    //web 标识浏览器 app server _src
            getCP(uid),
            getEP(option),
            getData(option)
        ].join("&");
       
        createRequest(prtl + 'log.sina.cn/sle.gif?'+ str);
        
        
    })
    
}
window.sinaLog=sinaLog;
export default sinaLog


