//     

import Evercookie from './evercookie'

const store = {}

export default {

  _retry: 10,

  setRetryTime: function (time        )       {

    if(typeof parseInt(time) !== 'number') {
      console.log('传参有误，默认使用10次重试')
      return
    }

    this._retry = time

  },

  get: function (key        , cb          )       {

    if (!key || key == '') {
      return
    }

    let ec
    if (!(key in store)) {

      store[key] = new Evercookie({
        tests: this._retry
      })
    }

    ec = store[key]

    ec.get(key, cb)

  },
  set: function (key        , value        )       {

    if (!key || key == '') {
      return
    }

    let ec
    if (!(key in store)) {

      store[key] = new Evercookie({
        tests: this._retry
      })
    }

    ec = store[key]

    ec.set(key, value)

  },

  getMany: function (keys                   , cb          )       {

    if (!keys.length || !('forEach' in keys)) {
      return
    }

    let num = keys.length

    const result = {}

    keys.forEach((key) => {

      if(typeof key !== 'string') {
        key = key.toString()
      }

      this.get(key, handleMultiCallback)

      function handleMultiCallback(data        )       {

        num--

        result[key] = data

        if (num == 0) {
          return cb(result)
        }
      }

    })
    
  }

}


