  getTrulyURL: function(url) {
    var truly = '';

    function filter(url, by) {
      var parts = url.split(by);
      var i = 0;
      var len = parts.length;
      var trulyURL = '';
      var part = '';
      var trulyParts = [];

      for (i = 0; i < len; i++) {
        part = parts[i];

        if ((part.indexOf('vt=') == 0) || (part.indexOf('pos=') == 0) || (part.indexOf('wm=') == 0)) {
          continue;
        }

        trulyParts[i] = part;
      }

      trulyURL = trulyParts.join(by);

      return trulyURL;
    }

    // 去掉vt=4&pos=108的情况
    truly = filter(url, '&');

    // 去掉html?vt=4的情况
    truly = filter(truly, '?');

    return truly;
  },
