import '@mfelibs/base-css';
import '@mfelibs/common_feed';
import '../../css/main_all.css';

// import '../../shared/suda_map.js';
// suda_init(sudaMapConfig.pageId, 100, sudaMapConfig.version||'');

import SIMA from '@mfelibs/base-tools-SIMA';
SIMA.init(sudaMapConfig.pageId, 100, sudaMapConfig.version||'');

import S from '../../shared/utils.js';
S.storage.checkfastmode();

import '@mfelibs/base-tools-addhistoryurl';

import sinaSax from '../../shared/sina_sax.js';
setTimeout(function() { //解决焦点图偶现插入失败问题。
  sinaSax.init();
}, 100);
sinaSax.bindEvent();

import './feed_push_zn.js';

import '../../shared/newExposure.min.js';

import '../../shared/nph_v1.min.js';

import sina_intellect_V from './intellect_v.js';
sina_intellect_V._init();

import navlocal from '../../shared/navlocal.js';
S.onload(function(){
  navlocal.init();
});

import '../../shared/quset_mobile.js';
// import '@mfelibs/base-keeplive';

import '../../shared/dynamicNav.js';

import '../../shared/uc_feed.js';

// 指纹识别
import userUnique from '@mfelibs/user-unique'
window.getUserInfo(function(rs){
  userUnique()
  if(S.isDebug()){console.log("执行指纹识别逻辑")}
})

// PWA
if ('serviceWorker' in navigator) {
  navigator.serviceWorker
  .register('./sw.js')
  .then(function () {
    console.log('Service Worker Registered');
  })
  .catch(function (err) {
    console.log(err);
  })
}


