// import ucJssdk from './uc_jssdk.js';
import lazyLoad from '@mfelibs/base-tools-lazyload';
import template from './template.js';
import webUa from '@mfelibs/base-tools-ua';
import newExposure from '@mfelibs/base-tools-exposure';

let indexNum = false;
let ucFeedData = '';
let isOnladData = false;
let posItem = [];
let orderNum = 0;
let isConsole = location.href.indexOf("ucDebug=1") != -1 ? true : false;
let UTP = window.UTPJSSDK || function(){};
let utp = new UTP({});
let ucFeed = {
  getSmartPos : function(){
    let feedUcNum = []
    if(SM && SM.data && SM.data.qudao && SM.data.qudao.all_pos && SM.data.qudao.all_pos.feed){
      let ucPos = SM.data.qudao.all_pos.feed.uc;
      if(ucPos && ucPos.length != 0){
        feedUcNum = ucPos;
      }
    }
    let ucFeedObj = [];
    if(feedUcNum && Array.isArray(feedUcNum)){
      for(let uci = 0; uci < feedUcNum.length; uci ++){ 
        //置顶特殊处理
        if(SM && SM.data && SM.data.feed[0] && SM.data.feed[0].length && feedUcNum[uci] >= SM.data.feed[0].length){
          feedUcNum[uci] = feedUcNum[uci] - SM.data.feed[0].length;
        }
        if(feedUcNum[uci] <= 1){
          feedUcNum[uci] = 1
        }
        ucFeedObj.push({"num":parseInt(feedUcNum[uci]) - 1,"isload":true});
      }
    }
    return ucFeedObj;
  },
  getTouchPos : function(){
    let feedUcNum = [];
    if(SM && SM.data && SM.data.qudao && SM.data.qudao.all_pos && SM.data.qudao.all_pos.touch){
      let ucPos = SM.data.qudao.all_pos.touch.uc;
      if(ucPos && ucPos.length != 0){
        feedUcNum = ucPos;
      }
    }
    let ucFeedObj = [];
    if(feedUcNum && Array.isArray(feedUcNum)){
      for(let uci = 0; uci < feedUcNum.length; uci ++){
        if(feedUcNum[uci] <= 1){
          feedUcNum[uci] = 1
        }
        ucFeedObj.push({"num":parseInt(feedUcNum[uci]) - 1,"isload":true});
      }
    }
    return ucFeedObj;
  },
  init : function(dom,pos){
    
    if(!window.UTPJSSDK) return;
    if(webUa.platformInfo.client == 'uc' || webUa.platformInfo.system != 'android'){
      return false;
    }
    let self = this;
    posItem.push({"order":orderNum,"pos":pos});
    // posItem.push(pos);
    orderNum ++;
    $(window).on("ucJsonpOnload",function(){
      isOnladData = true;
      self.adaptWork();
    })

    if(isOnladData){
      self.adaptWork();
      return false;
    }

    if(indexNum || posItem.length === 0) return false;

    
    this.dom = dom || 'j_card_intenews';
    utp.load({
      cid : 112,
      limit :4
    })
    .then(function(ads){
      if(ads && ads.result && Array.isArray(ads.result) && ads.result.length > 0){
        ucFeedData = ads.result;
        $(window).trigger("ucJsonpOnload");
      }
      if(isConsole){
        console.log(ads,"ucFeed返回数据");
      }
    })
    .catch(function(err){
      console.log(err,"ucfeed加载失败！")
    })
    indexNum = true;
  },
  adaptWork: function(){
    let self = this;
    if(ucFeedData.length == 0 || posItem.length == 0) return false;
    for(let k = 0; k < posItem.length; k ++){
      if(!ucFeedData[posItem[k].order]) continue;
      self.adapt(ucFeedData[posItem[k].order],posItem[k].pos);
    }
    posItem = [];
  },
  adapt : function(data,pos){
    let info = {}
    let self = this;
    info.title = data.title || '';
    info.link = data.circle_page || '';
    info.adid = data.adid || '';
    info.comment = 0;
    let htmlsrt = '';
    if(Array.isArray(data.AssetImages) && data.AssetImages.length >= 3){
      info.imgs = [];
      for(let i = 0;i < 3;i ++){
        info.imgs.push(data.AssetImages[i].url.replace("http:","https:"));
      }
      if(self.dom == 'j_card_intenews'){
        info.tag = '<mark class="cm_mark cm_mark_r">图集</mark>';
        var dataObj = {
          info : info,
          type: "feeduc"
        }
        htmlsrt = template.Ztemplate.threePicFnTem(dataObj)
      }else{
        htmlsrt = template.get3PicsFeedStr("feeduc",info,false,false,true)
      }
    }else{
      info.img = data.AssetImages[0] && data.AssetImages[0].url.replace("http:","https:") || '';
      if(self.dom == 'j_card_intenews'){
        var dataObj = {
          info : info,
          type: "feeduc"
        }
        htmlsrt = template.Ztemplate.picTxtFnTem(dataObj);
      }else{
        htmlsrt = template.getCommonFeedStr("feeduc",info,false,false,true);
      }
    }

    $("#"+self.dom).children("a").eq(pos).get(0).insertAdjacentHTML("beforeBegin",htmlsrt);
    self.resetAD($("#"+self.dom).children("a").eq(pos));
    lazyLoad && lazyLoad.currentLazy();
    var ucFeedDom = $("#"+self.dom).children("a").eq(pos).get(0);
    var ucFeedArr = [];
    ucFeedArr.push(ucFeedDom)
    newExposure.pushListeningEle(ucFeedArr,function(arr){
      window.SIMA({
        action : "_exposure",
        pk : '187523',
        data :{
          "index":pos,
          "aid":"ucFeed",
          "url":encodeURIComponent(info.link)
        }
      });
    })
    $("#"+self.dom).children("a").eq(pos).on('click',function(){
      window.SIMA({
        action : "_click",
        pk : '187523',
        data :{
          "index":pos,
          "aid":"ucFeed",
          "url":encodeURIComponent(info.link)
        }
      });
      utp.click(info.adid);
    })
  },
  resetAD : function(targetEle){
    var index = targetEle.index()+1;
    var domBox = {};
    if($('#j_card_intenews').length == 0){
      domBox = $("#j_card_like_feed");
    }else{
      domBox = $("#j_card_intenews");
    }
    var feeds = domBox.children();
    for(var i=index;i<feeds.length;i++){
      if(feeds.eq(i).data("style") == 'client'){
        feeds.eq(i).insertBefore(feeds.eq(i).prev());
      }
    }
  }
}
export default ucFeed




