const doc = document
const win = window

class AddHistoryUrl {
  constructor(obj) {
    obj = obj ? obj : {}
    // 浏览器回退的地址
    this.wm = ''
    this.channelUrl =
      obj.channelUrl ||
      location.protocol +
        '//' +
        location.host +
        this.routeName() +
        this.wmValue() +
        'chname=' +
        this.channelName()
    this.startTime = 0
    this.endTime = 0
    this.blackListArray = [
      'wm=3206',
      'wm=3255',
      'wm=3242',
      'wm=3208',
      'wm=3315',
      'wm=3312',
      'wm=3402',
      '51xiancheng.com',
      '3g.cn',
      'baidu'
    ]

    this.init()
  }

  init() {
    this.goReturnChannel()
  }

  // 执行回退操作
  goReturnChannel() {
    this.startTime = new Date().getTime()
    if (this.HasHistoryUrl()) {
      if (this.isOldAdd() && this.isChannel() && this.isHomeSource()) {
        console.log('oncehis')
        var hostLink = this.channelUrl + '&times=1'
        var liveLink = location.href
        if (this.isHomeId()) {
          // 浪首特定逻辑处理
          hostLink = location.search
            ? location.href + '&urlHistory=sina'
            : location.href + '?vt=4&urlHistory=sina'
        }
        history.replaceState(null, '', hostLink)
        history.pushState('', '', liveLink)
        window.addEventListener(
          'popstate',
          function() {
            this.endTime = new Date().getTime()
            if (this.endTime - this.startTime > 200) {
              location.reload()
            }
          }.bind(this)
        )
      } else if (!this.isHomeId() && !this.isOldAdd()) {
        console.log('twicehis')
        var hostLink = this.channelUrl + '&times=2'
        var liveLink = location.href
        history.replaceState(null, '', hostLink)
        history.pushState('', '', liveLink)
        window.addEventListener(
          'popstate',
          function() {
            this.endTime = new Date().getTime()
            if (this.endTime - this.startTime > 200) {
              location.reload()
            }
          }.bind(this)
        )
      }
    }
  }

  routeName() {
    var routeStr = '/intercept.d.html/?'
    var hostName = location.hostname
    // 图集页路由更改;
    if (hostName == 'photo.sina.cn' || hostName == 'k.sina.cn') {
      routeStr = '/intercept_d.html/?'
    }
    return routeStr
  }

  //判断是否添加回退功能
  HasHistoryUrl() {
    return (
      history.pushState &&
      !appType.Utils.isSinaNews() &&
      !appType.Utils.isQQ() &&
      !appType.Utils.isWeibo() &&
      this.backEnd() &&
      this.isBlackList()&&
      this.isStopHis()
    )
  }

  wmValue() {
    if (this.getUrlString('wm') != '' && this.getUrlString('wm') != null) {
      this.wm = 'wm=' + this.getUrlString('wm') + '&'
    }
    return this.wm
  }

  // 获取地址栏的参数
  getUrlString(paraName) {
    var reg = new RegExp('(^|&)' + paraName + '=([^&]*)(&|$)', 'i')
    var r = window.location.search.substr(1).match(reg)
    if (r != null) return r[2]
    return null
  }

  // 获取网址的名称标识
  channelName() {
    var hostname = location.hostname
    return hostname.split('.')[0]
  }

  isBlackList() {
    var _this = this
    var blackList = _this.blackListArray
    var blackListLen = blackList.length
    for (var i = 0; i < blackListLen; i++) {
      if (
        blackList[i].indexOf('wm=') >= 0 &&
        doc.location.href.indexOf(blackList[i]) >= 0
      ) {
        return false
      } else if (doc.referrer.indexOf(blackList[i]) >= 0) {
        return false
      }
    }
    return true
  }

  isHomeId() {
    // 浪首屏蔽二次回退功能
    return location.hostname == 'sina.cn'
  }

  isChannel() {
    return doc.referrer.indexOf(this.channelName()) == -1
  }

  backEnd() {
    return doc.location.href.indexOf('backToHome=') == -1
  }

  isOldAdd() {
    return doc.location.href.indexOf('urlHistory=' + this.channelName()) == -1
  }
  isStopHis(){
      return doc.location.href.indexOf("his=0") == -1;
  }

  isHomeSource() {
    return doc.referrer.indexOf('sina.cn') == -1
  }
}

var appType = {}
appType.Utils = {
  _UA: navigator.userAgent.toLowerCase(),
  UAIdentify: {
    weibo: 'weibo',
    qq: 'qq/',
    sinanews: 'sinanews'
  },
  isInclude: function(allStr, str) {
    return allStr.indexOf(str) > -1
  },
  isSinaNews: function() {
    var _all = this.UAIdentify,
      _isSinaNews = this.isInclude(this._UA, _all.sinanews)
    if (_isSinaNews) {
      return true
    } else {
      return false
    }
  },
  isWeibo: function() {
    var _all = this.UAIdentify,
      _isWeibo = this.isInclude(this._UA, _all.weibo)
    if (_isWeibo) {
      return true
    }
    return false
  },
  isQQ: function() {
    var _all = this.UAIdentify,
      _isQQ = this.isInclude(this._UA, _all.qq)
    if (_isQQ) {
      return true
    } else {
      return false
    }
  }
}

window.addEventListener('load', function() {
  const addHistory = new AddHistoryUrl()
  window.addEventListener('pageshow', function() {
   addHistory.startTime = new Date().getTime();
  })
})


export default AddHistoryUrl



