/*
 基础组件——下拉刷新
 dependence: @mfelibs/base-utils
 */
import { $, BaseModule } from "@mfelibs/base-utils";
import "./index.css";

const defaultOption = {
  circleColor: "#299df2",
  shadowColor: "#e8e8e8",
  $extraToggle: undefined,
  drawHeight: 50,
  afterExcuteReturn: () => {},
  beforeDrawStart: () => {}
};

class DrawRefresh extends BaseModule {
  constructor(el, option, israise = false) {
    super(el, option, israise);
    this.option = this.getOptions(defaultOption);
    this.$extraToggleInit();
    this._init();
  }

  abort() {
    this._upReturn();
    this.isAbort = true;
  }

  goOn() {
    this.isAbort = false;
  }

  _init() {
    this._insertLoading();
    this._setStyle();
    this._bindEvent();
  }

  $extraToggleApply(applyCb) {
    if (this.option.$extraToggle) {
      this.option.$extraToggle.forEach($toggle => {
        applyCb($toggle);
      });
    }
  }

  $extraToggleInit() {
    if (this.option.$extraToggle) {
      this.option.$extraToggle = this.option.$extraToggle.map(function(item) {
        return $(item);
      });
    }
  }

  _bindEvent() {
    this.$el.on("touchmove", this._touchMove.bind(this));
    this.$el.on("touchstart", this._touchStart.bind(this));
    this.$el.on("touchend", this._touchEnd.bind(this));

    this.$extraToggleApply($toggle => {
      $toggle.on("touchmove", this._touchMove.bind(this));
      $toggle.on("touchstart", this._touchStart.bind(this));
      $toggle.on("touchend", this._touchEnd.bind(this));
    });
  }

  _setStyle() {
    // this.$el.prev().css({
    //   "z-index": 1
    // });
    // this.$el.css({
    //   "z-index": 0
    // });

    this.$el.css({
      transform: "translate3d(0px, 0px, 0px)",
      "-webkit-transform": "translate3d(0px, 0px, 0px)",
      "-moz-transform": "translate3d(0px, 0px, 0px)",
      "-ms-transform": "translate3d(0px, 0px, 0px)",
      "-o-transform": "translate3d(0px, 0px, 0px)"
    });

    this.$extraToggleApply($toggle => {
      $toggle.css({
        transform: "translate3d(0px, 0px, 0px)",
        "-webkit-transform": "translate3d(0px, 0px, 0px)",
        "-moz-transform": "translate3d(0px, 0px, 0px)",
        "-ms-transform": "translate3d(0px, 0px, 0px)",
        "-o-transform": "translate3d(0px, 0px, 0px)"
      });
    });

    this.$loadingContent.css({
      clip: "rect( 50px 1000px 50px 0px)"
    });
    this.$loadingContent.find(".loading-circle-check").css({
      "border-color": this.option.circleColor
    });
    this.$loadingContent.find(".loading-circle").css({
      "border-color": this.option.circleColor
    });
    this.$loadingContent.find(".loading-circle-shadow").css({
      background: this.option.shadowColor
    });
    this.$loadingContent.find(".loading-circle-check-shadow").css({
      background: this.option.shadowColor
    });
  }

  _move(distance) {
    !this.isdrawing && this.drawingStart();
    this._circleExtend(distance * 0.34);
    let moveDistance = distance * 0.6;
    if (moveDistance > this.option.drawHeight) {
      return;
    }
    this.$el.css({
      transform: "translate3d(0px, " + moveDistance + "px, 0px)",
      "-webkit-transform": "translate3d(0px, " + moveDistance + "px, 0px)",
      "-moz-transform": "translate3d(0px, " + moveDistance + "px, 0px)",
      "-ms-transform": "translate3d(0px, " + moveDistance + "px, 0px)",
      "-o-transform": "translate3d(0px, " + moveDistance + "px, 0px)"
    });

    this.$extraToggleApply($toggle => {
      $toggle.css({
        transform: "translate3d(0px, " + moveDistance + "px, 0px)",
        "-webkit-transform": "translate3d(0px, " + moveDistance + "px, 0px)",
        "-moz-transform": "translate3d(0px, " + moveDistance + "px, 0px)",
        "-ms-transform": "translate3d(0px, " + moveDistance + "px, 0px)",
        "-o-transform": "translate3d(0px, " + moveDistance + "px, 0px)"
      });
    });

    this.$loadingContent.css({
      clip: "rect(" + (50 - moveDistance) + "px 1000px 50px 0px)"
    });
  }

  excuteReturn() {
    this._excuteReturn();
  }

  _excuteReturn() {
    this.$el.css({
      transition: "all 300ms",
      "-webkit-transition": "all 300ms",
      "-moz-transition": "all 300ms",
      "-ms-transition": "all 300ms",
      "-o-transition": "all 300ms",
      transform: "translate3d(0px, 0px, 0px)",
      "-webkit-transform": "translate3d(0px, 0px, 0px)",
      "-moz-transform": "translate3d(0px, 0px, 0px)",
      "-ms-transform": "translate3d(0px, 0px, 0px)",
      "-o-transform": "translate3d(0px, 0px, 0px)"
    });
    this.$extraToggleApply($toggle => {
      $toggle.css({
        transition: "all 300ms",
        "-webkit-transition": "all 300ms",
        "-moz-transition": "all 300ms",
        "-ms-transition": "all 300ms",
        "-o-transition": "all 300ms",
        transform: "translate3d(0px, 0px, 0px)",
        "-webkit-transform": "translate3d(0px, 0px, 0px)",
        "-moz-transform": "translate3d(0px, 0px, 0px)",
        "-ms-transform": "translate3d(0px, 0px, 0px)",
        "-o-transform": "translate3d(0px, 0px, 0px)"
      });
    });
    this.$loadingContent.css({
      transition: "all 300ms",
      "-webkit-transition": "all 300ms",
      "-moz-transition": "all 300ms",
      "-ms-transition": "all 300ms",
      "-o-transition": "all 300ms",
      clip: "rect( 50px 1000px 50px 0px)"
    });

    setTimeout(() => {
      this.inLoading = false;

      this.$el.css({
        transition: "none",
        "-webkit-transition": "none",
        "-moz-transition": "none",
        "-ms-transition": "none",
        "-o-transition": "none"
      });
      this.$extraToggleApply($toggle => {
        $toggle.css({
          transition: "none",
          "-webkit-transition": "none",
          "-moz-transition": "none",
          "-ms-transition": "none",
          "-o-transition": "none"
        });
      });
      this.$loadingContent.css({
        transition: "none",
        "-webkit-transition": "none",
        "-moz-transition": "none",
        "-ms-transition": "none",
        "-o-transition": "none",
        display: "block"
      });

      this._circleClear();
      this.drawingEnd();

      this.option.afterExcuteReturn();
    }, 300);
  }

  _upReturn(beforeReturn) {
    this.$loadingContent.css({
      display: "none"
    });

    if (beforeReturn) {
      beforeReturn();
      return;
    }

    this._excuteReturn();
  }

  _successUpReturn(beforeReturn) {
    this._circleDone();

    setTimeout(() => {
      this._upReturn(beforeReturn);
    }, 1000);
  }

  _errorUpReturn() {
    setTimeout(() => {
      this._upReturn();
    }, 400);
  }

  successUp(beforeReturn) {
    this._successUpReturn(beforeReturn);
  }

  errorUp() {
    this._errorUpReturn();
  }

  upReady() {
    this.inLoading = true;
    this.$el.css({
      transition: "all 400ms",
      "-webkit-transition": "all 400ms",
      "-moz-transition": "all 400ms",
      "-ms-transition": "all 400ms",
      "-o-transition": "all 400ms",
      transform: "translate3d(0px, 40px, 0px)",
      "-webkit-transform": "translate3d(0px, 40px, 0px)",
      "-moz-transform": "translate3d(0px, 40px, 0px)",
      "-ms-transform": "translate3d(0px, 40px, 0px)",
      "-o-transform": "translate3d(0px, 40px, 0px)"
    });
    this.$extraToggleApply($toggle => {
      $toggle.css({
        transition: "all 400ms",
        "-webkit-transition": "all 400ms",
        "-moz-transition": "all 400ms",
        "-ms-transition": "all 400ms",
        "-o-transition": "all 400ms",
        transform: "translate3d(0px, 40px, 0px)",
        "-webkit-transform": "translate3d(0px, 40px, 0px)",
        "-moz-transform": "translate3d(0px, 40px, 0px)",
        "-ms-transform": "translate3d(0px, 40px, 0px)",
        "-o-transform": "translate3d(0px, 40px, 0px)"
      });
    });
    this.$loadingContent.css({
      clip: "rect( 10px 1000px 50px 0px)"
    });
  }

  _upRefresh(refresh, isAuto = false) {
    isAuto &&
      this.$loadingContent.css({
        display: "none"
      });
    this.upReady();
    setTimeout(() => {
      isAuto && this.$loadingContent.css({ display: "block" });
      this._circleRotate();
      if (refresh) {
        refresh && refresh(this);
      } else {
        this.option.refresh(this);
      }
    }, 400);
  }

  drawingStart() {
    if (this.isdrawing) {
      return;
    }
    this.isdrawing = true;
    let color = $(document.body).css("background-color");
    this.preBodyColor = color;
    $(document.body).css("background-color", this.option.shadowColor);
  }

  drawingEnd() {
    $(document.body).css("background-color", this.preBodyColor);
    this.isdrawing = false;
  }

  _touchStart(e) {
    if (this.isAbort || this.inLoading) {
      return;
    }

    this.touchStartPageY = e.changedTouches[0].pageY;
    this.preTouchPageY = this.touchStartPageY;
    this.option.beforeDrawStart();
  }

  _touchMove(e) {
    if (this.isAbort || this.inLoading) {
      return;
    }

    this.haveMove = true;
    let touchMovePageY = e.changedTouches[0].pageY;
    let touchDistance = touchMovePageY - this.touchStartPageY;
    //判断是否是顶部下拉
    let scrollTop =
      document.documentElement.scrollTop || document.body.scrollTop;
    if (touchDistance > 0 && scrollTop == 0) {
      if (touchDistance > 86) {
        //isRefresh 是否可以刷新
        this.isRefresh = true;
      } else {
        this.isRefresh = false;
      }
      this._move(touchDistance);
      this.preTouchPageY = touchMovePageY;
      e.preventDefault();
      e.stopPropagation();
    }
  }

  _circleClear() {
    this.$loadingContent.find(".loading-circle-shadow").css({
      top: 0
    });
    this.$loadingContent.find(".loadingR").removeClass("load");
    this.$loadingContent.find(".loadingR").removeClass("done");
  }

  _circleRotate() {
    this.$loadingContent.find(".loadingR").addClass("load");
  }

  _circleDone() {
    this.$loadingContent.find(".loadingR").removeClass("load");
    this.$loadingContent.find(".loadingR").addClass("done");
  }

  _circleExtend(height) {
    //限制遮罩最大高度
    if (height > 60) {
      return;
    }
    this.$loadingContent.find(".loading-circle-shadow").css({
      top: height + "px"
    });
  }

  _touchEnd(e) {
    if (this.isAbort || this.inLoading) {
      return;
    }

    if (this.haveMove && this.isRefresh && document.body.scrollTop == 0) {
      this._upRefresh();
    } else {
      this._upReturn();
    }
    this.haveMove = false;
    this.isRefresh = false;
  }

  _insertLoading() {
    const loadingHtml = `<div class="dr-loading">
                                <div class="dr-loading-content">
                                    <div class="loadingR">
                                        <div class="loading-circle-shadow"></div>
                                        <div class="loading-circle"></div>
                                        <div class="loading-circle-check"></div>
                                        <div class="loading-circle-check-shadow"></div>
                                    </div>
                                </div>
                            </div>`;
    this.$loading = $(loadingHtml);
    this.$loadingContent = this.$loading.children().eq(0);
    this.$el.prepend(this.$loading);
  }

  //自动模拟下拉刷新
  autoDrawRefresh(refresh) {
    let touchDistance = 0;
    let autoDrawInterval = setInterval(() => {
      if (touchDistance >= 182) {
        clearInterval(autoDrawInterval);
        this._upRefresh(refresh, true);
        return;
      }
      this._move(touchDistance);
      touchDistance += 5;
    }, 5);
  }

  //自动下拉刷新
  autoRefresh(refresh) {
    this.drawingStart();
    this.$loadingContent.find(".loading-circle-shadow").css({
      top: "30px"
    });
    this._upRefresh(refresh, true);
  }
}

BaseModule.register(DrawRefresh, "DrawRefresh");

export default DrawRefresh;



