      str += "<a " + target + otherArgs + " data-scheme='" + info.scheme + "' data-href='" + info.link + "'" + (info.info ? " data-info='" + info.info + "'" : "") + " href='javascript:void(0);' title='" + info.title + "' class='m_f_a cn " + liteCellName + "'>";
