  isAndroidNoQQ : function(){
    var ua = navigator.userAgent.toLowerCase();
    if(ua.indexOf("Android") != -1 || ua.indexOf("android") != -1){
      if(ua.indexOf("qq") != -1 || ua.indexOf("QQ") != -1 ){
        return false
      }else{
        return true;
      }
    }else{
      return false;
    }
  },
