// jshint esversion:6
import lazyLoad from '@mfelibs/base-tools-lazyload';
import Swiper from '@mfelibs/base-tools-swiper';
import newExposure from '@mfelibs/base-tools-exposure';

const doc = document;
const win = window;
let isFirst = false;

let goodColumn = {
  init() {
    if(isFirst || $("#j_card_intenews").length == 0 || !SM || !SM.data || !SM.data.jinpinlanmu || !SM.data.jinpinlanmu.list || !SM.data.jinpinlanmu.feed_pos || SM.data.jinpinlanmu.list.length < 4 || parseInt(SM.data.jinpinlanmu.feed_pos) < 1) return false;
    this.feedPosFn();
    if(this.feedPos >= $("#j_card_intenews").children("a").length) return false;
    this.appendHtml();
    this.swift();
    this.exeposure();
  },
  feedPosFn (){
    let pagePos = parseInt(SM.data.jinpinlanmu.feed_pos);
    if(SM.data.feed[0] && SM.data.feed[0].length){
      if(pagePos > SM.data.feed[0].length){
        this.feedPos = pagePos - SM.data.feed[0].length - 1;
      }else{
        this.feedPos = 0;
      }
    }else{
      this.feedPos = pagePos - 1;
    }
    
  },
  appendHtml (){
    let htmlStr = '';
    let dataObj = SM.data.jinpinlanmu.list;
    let self = this;
    htmlStr += '<section class="gl_nav_list" id="j_goodColumn" data-sudaclick="goodColumnExp">';
    htmlStr += '<ul class="gl_nav_list_ul">';
    for(let i = 0,len = dataObj.length; i < len; i ++){
      htmlStr +=`<li class="gl_nav_list_li">
                  <a href="${dataObj[i].link.url}">
                      <figure class="gl_nav_list_pic i_bg">
                        <img class="gl_nav_list_img" data-src="${dataObj[i].imgurl.replace("http:","https:")}" src="data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAMAAAACAQMAAACnuvRZAAAAA1BMVEUAAACnej3aAAAAAXRSTlMAQObYZgAAAApJREFUCNdjAAIAAAQAASDSLW8AAAAASUVORK5CYII=">
                      </figure>
                      <h2 class="gl_nav_list_title">${dataObj[i].link.title}</h2>
                  </a>
              </li>`
    }
    $("#j_card_intenews").children("a").eq(self.feedPos).get(0).insertAdjacentHTML("beforeBegin",htmlStr);
    isFirst = true;
  },
  exeposure (){
    if($("#j_goodColumn").length == 0) return false;
    newExposure.pushListeningEle([$("#j_goodColumn").get(0)],function(arr){
      if (window.SIMA) {       
        var clickInfo = {
          'type' : 'goodColumn',  //曝光的类型
          'name' : 'goodColumnExp',  //当前点击的aid关键字 
          'title': '精品栏目曝光', //中文说明
          'index': 0  //索引  如果是单个为0即可
        }
        if(window.SIMA.suda_count){
          window.SIMA.suda_count(clickInfo);
        }
      };
    })
  },
  swift(){
    if($("#j_goodColumn").length == 0) return false;
    new Swiper('#j_goodColumn', {
      wrapperClass:"gl_nav_list_ul",
      slideClass : 'gl_nav_list_li',
      freeMode : true,
      paginationClickable: true,
      slidesPerView : 'auto',
      onTouchEnd : function(swiper){
        lazyLoad && lazyLoad.currentLazy();
      }
    })
  }
};

export default goodColumn;



