    jsTemplate: (function() {
      var cache = {},
        foo = function tmpl(str, data) {
          var fnArrStr = [];
          fnArrStr.push("var p=[],print=function(){p.push.apply(p,arguments);};")
          fnArrStr.push("with(obj){p.push('")
          fnArrStr.push(str.replace(/[\r\t\n]/g, " ")
            .split("<%").join("\t")
            .replace(/((^|%>)[^\t]*)'/g, "$1\r")
            .replace(/\t(.*?)%>/g, "',$1,'")
            .split("\t").join("');")
            .split("%>").join("p.push('")
            .split("\r").join("\\'"))
          fnArrStr.push("');}return p.join('');")
          var fn = !/\W/.test(str) ?
            cache[str] = cache[str] ||
            tmpl(document.getElementById(str).innerHTML) :
            new Function("obj",
              fnArrStr.join(""));

          return data ? fn(data) : fn;
        };
      return foo
    })(),
