// import ucJssdk from './uc_jssdk.js';
import S from './utils.js';
import lazyLoad from '@mfelibs/base-tools-lazyload';
import template from './template.js';
import webUa from '@mfelibs/base-tools-ua';
import newExposure from '@mfelibs/base-tools-exposure';

let indexNum = false;
let iqiyiFeedData = '';
// let isOnladData = false;
let posItem = [];
let orderNum = 0;
let isConsole = location.href.indexOf("iqiyiDebug=1") != -1 ? true : false;
let iqiyiFeed = {
  getSmartPos : function(){
    let feedIqiyiNum = []
    if(SM && SM.data && SM.data.qudao && SM.data.qudao.all_pos && SM.data.qudao.all_pos.feed){
      let iqiyiPos = SM.data.qudao.all_pos.feed.iqiyi;
      if(iqiyiPos && iqiyiPos.length != 0){
        feedIqiyiNum = iqiyiPos;
      }
    }
    let iqiyiFeedObj = [];
    if(feedIqiyiNum && Array.isArray(feedIqiyiNum)){
      for(let uci = 0; uci < feedIqiyiNum.length; uci ++){
        if(feedIqiyiNum[uci] <= 2){
          feedIqiyiNum[uci] = 2
        }
        iqiyiFeedObj.push({"num":parseInt(feedIqiyiNum[uci]) - 2,"isload":true});
      }
    }
    return iqiyiFeedObj;
  },
  getTouchPos : function(){
    let feedIqiyiNum = [];
    if(SM && SM.data && SM.data.qudao && SM.data.qudao.all_pos && SM.data.qudao.all_pos.touch){
      let iqiyiPos = SM.data.qudao.all_pos.touch.iqiyi;
      if(iqiyiPos && iqiyiPos.length != 0){
        feedIqiyiNum = iqiyiPos;
      }
    }
    let iqiyiFeedObj = [];
    if(feedIqiyiNum && Array.isArray(feedIqiyiNum)){
      for(let uci = 0; uci < feedIqiyiNum.length; uci ++){
        if(feedIqiyiNum[uci] <= 1){
          feedIqiyiNum[uci] = 1
        }
        iqiyiFeedObj.push({"num":parseInt(feedIqiyiNum[uci]) - 1,"isload":true});
      }
    }
    return iqiyiFeedObj;
  },
  init : function(dom,pos){
    if(webUa.platformInfo.system != 'android' ||!SM || !SM.data || !SM.data.qudao || !SM.data.qudao.iqiyi || SM.data.qudao.iqiyi.length == 0) return false;

    let self = this;
    this.dom = dom || 'j_card_intenews';

    iqiyiFeedData = SM.data.qudao.iqiyi[0];

    orderNum = parseInt(Math.random()*iqiyiFeedData.length);
    console.log(orderNum)
    
    posItem.push({"order":orderNum,"pos":pos});
    if(isConsole){
      console.log(SM.data.qudao.iqiyi,"奇艺数据")
    }
    self.adaptWork();
  },
  adaptWork: function(){
    let self = this;
    if(iqiyiFeedData.length == 0 || posItem.length == 0) return false;
    for(let k = 0; k < posItem.length; k ++){
      if(!iqiyiFeedData[posItem[k].order]) continue;
      self.adapt(iqiyiFeedData[posItem[k].order],posItem[k].pos);
    }
    posItem = [];
  },
  adapt : function(data,pos){
    let info = {}
    let self = this;
    info.title = data.title || '';
    info.intro = data.intro || '';
    info.link = data.link || '';
    info.comment = 0;
    let htmlsrt = '';
    if(data && data.pics && data.pics.img_original && data.pics.img_original.length >= 3){
      info.imgs = [];
      
      if(self.dom == 'j_card_intenews'){
        info.tag = '<mark class="cm_mark cm_mark_r">图集</mark>';
        for(let i = 0;i < 3;i ++){
          info.imgs.push(S.getCuttedImg(data.pics.img_original[i],"230_154",true));
        }
        var dataObj = {
          info : info,
          type: "feediqiyi"
        }
        htmlsrt = template.Ztemplate.threePicFnTem(dataObj)
      }else{
        for(let i = 0;i < 3;i ++){
          info.imgs.push(data.pics.img_original[i]);
        }
        htmlsrt = template.get3PicsFeedStr("feediqiyi",info,false,false,false,"feediqiyi")
      }
    }else{

      if(self.dom == 'j_card_intenews'){
        info.img = S.getCuttedImg(data.img,'200_134', true) || '';
        var dataObj = {
          info : info,
          type: "feediqiyi"
        }
        htmlsrt = template.Ztemplate.picTxtFnTem(dataObj);
      }else{
        info.img = data.img || '';
        htmlsrt = template.getCommonFeedStr("iqiyiuc",info,false,false,false,"feediqiyi");
      }
    }

    $("#"+self.dom).children("a").eq(pos).get(0).insertAdjacentHTML("beforeBegin",htmlsrt);
    lazyLoad && lazyLoad.currentLazy();
    self.resetAD($("#"+self.dom).children("a").eq(pos));
    var ucFeedDom = $("#"+self.dom).children("a").eq(pos).get(0);
    var ucFeedArr = [];
    ucFeedArr.push(ucFeedDom)
    newExposure.pushListeningEle(ucFeedArr,function(arr){
      window.SIMA({
        action : "_exposure",
        pk : '187523',
        data :{
          "index":pos,
          "aid":"iqiyiFeed",
          "url":encodeURIComponent(info.link)
        }
      });
    })
    $("#"+self.dom).children("a").eq(pos).on('click',function(){
      window.SIMA({
        action : "_click",
        pk : '187523',
        data :{
          "index":pos,
          "aid":"iqiyiFeed",
          "url":encodeURIComponent(info.link)
        }
      });
      var config = {
          "androidInstallUrl":info.intro,
          "androidNativeUrl":info.link
      }
      _sinaCallEvent.trigger('sina_open_native',config);
    })
  },
  resetAD : function(targetEle){
    var index = targetEle.index()+1;
    var domBox = {};
    if($('#j_card_intenews').length == 0){
      domBox = $("#j_card_like_feed");
    }else{
      domBox = $("#j_card_intenews");
    }
    var feeds = domBox.children();
    for(var i=index;i<feeds.length;i++){
      if(feeds.eq(i).data("style") == 'client'){
        feeds.eq(i).insertBefore(feeds.eq(i).prev());
      }
    }
  }
}
export default iqiyiFeed




