import creative1 from "./creative1.js";

const newIdeaAd = {
    isReady : false,
    workerList : [],
    pluginInfo : {
        src : 'https://d1.sina.com.cn/litong/path/new/creative1.js',
        // src : 'https://d2.sina.com.cn/litong/path/new/creative1.js',
    },
    /**
     * 插入插件
     */
    getPlugin(){
        if(!!window.sinaCreative && !this.isReady){
            this.isReady = true;
            this.goWork();
        }else{
            let _s = document.createElement('script');
            let self = this;
            _s.src = this.pluginInfo.src;
            _s.onload = function(){
                self.isReady = true;
                self.goWork();
            };
            document.body.appendChild(_s);
        }
    },

    /**
     * 
     * @param {执行插入动效队列} aWork 
     */
    goWork (aWork){
        // debugger
        if( !!aWork ){
            this.workerList.push( aWork );
        }

        if(!this.isReady){
            this.getPlugin();
            return;
        }

        for(let i=this.workerList.length-1;i>=0;i--){
           let tmp =  this.workerList.splice(i);
          (sinaCreative = window.sinaCreative || []).push(tmp[0]);
        }
    },

    /**
     * 创建唯一标识
     */
    createId () {
            var s = [];
            var hexDigits = "0123456789abcdef";
            var idLen = 8;//idLen>8
            for (var i = 0; i < idLen; i++) {
                s[i] = hexDigits.substr(Math.floor(Math.random() * 0x10), 1);
            }
            // s[14] = "4"; // bits 12-15 of the time_hi_and_version field to 0010
            s[parseInt(idLen/2)+1] = hexDigits.substr((s[idLen/2] & 0x3) | 0x8, 1); // bits 6-7 of the clock_seq_hi_and_reserved to 01
            // s[8] = s[13] = s[18] = s[23] = "-";
            
            var uuid = s.join("");
            return '_'+uuid+(new Date).getTime().toString().substring(5,13);
    }
};
export default newIdeaAd;


