import wdBigPicObj from '@mfelibs/weidong-card';
import newExposure from '@mfelibs/base-tools-exposure';

let doc = document;
let win = window;
let num = 0;
const wdBigPic = {
  init(){
    this.isFirst = true;
    this.hasDom = true;
    this.wdObj = {};
    this.hasDomFn();
  },
  hasDomFn(){
    let len = $(".hasWdDom").length;
    if(len == 0){
      this.hasDom = false;
      return false;
    }
    this.hasDom = true;
    for(let i = 0;i < len; i ++){
      this.runCom();
    }
    this.exeposure();
    this.destroy();
  },
  runCom(){
    if(this.hasDom){
      if(this.isFirst){
        this.wdObj = new wdBigPicObj();
        console.log(this.wdObj);
        this.isFirst = false;
      }else{
        this.wdObj.addCard();
      }
    }
  },
  exeposure(){
    let exeArr = [];
    for(let j = 0; j < $(".hasWdDom").length;j ++){
      exeArr.push($(".hasWdDom").eq(j).get(0));
    }
    
    newExposure.pushListeningEle(exeArr,function(arr){
      if (window.SIMA) {
        var ExpInfo = {
          'type' : 'wdBigPicExp',  //曝光的类型
          'name' : 'wdBigPic',  //当前点击的aid关键字 
          'title': '微动大图曝光', //中文说明
          'index': num++ //索引  如果是单个为0即可
        }
        if(window.SIMA.suda_count){
          window.SIMA.suda_count(ExpInfo);
        }
      };
    })
  },
  destroy(){
    _sinaCallEvent && _sinaCallEvent.trigger("sina_bind_target");
    $(".m_f_a").removeClass("hasWdDom");
  }
};

export default wdBigPic;



