// jshint esversion:6

import S from './utils.js';

const doc = document;
const win = window;

class dynamicNav {
  constructor(){
    this.fixBoxDom = $("#j_dynamicNav");
    this.feedBoxDom = $("#j_card_tnews");
    this.changeFixBox = $("#j_changeFixBox");
    if (this.fixBoxDom.length == 0) return false;
    if (!SM.data.channels) return false;
    this.init();
  }
  init (){ 
    this.scrollPage();
  }
  scrollPage (){
    document.addEventListener('scroll',debounce(this.scrollEnd.bind(this)));
    document.addEventListener('scroll',this.scrollNav.bind(this));
  }
  scrollNav(){
    if (this.feedBoxDom.get(0).getBoundingClientRect().top <= 0) {
      if (!this.changeFixBox.hasClass("hide")) return false;
      if (this.fixBoxDom.html() != '') {
        this.changeFixBox.removeClass("hide");
      }
    } else {
      if (this.changeFixBox.hasClass("hide")) return false;
      this.changeFixBox.addClass("hide");
    }
  }
  scrollEnd(){
    let self = this;
    let feedItemDom = self.feedBoxDom.find("a");
    let len = feedItemDom.length;
    let fixNavDom = self.fixBoxDom;
    let chArr = '';
    let channelList = SM.data.channels;
    // 下划线
    // for (let i = len-1; i >= 0; i --) {
    //   let feedTop = feedItemDom.eq(i).get(0).getBoundingClientRect().top + feedItemDom.eq(i).height();
    //   if (feedTop <= fixNavDom.height()) {
    // 上划线
    for (let i = 0; i < len; i++) {
      let feedTop = feedItemDom.eq(i).get(0).getBoundingClientRect().top;
      if (feedTop >= fixNavDom.height()) {
        for (let p = 0, plen = channelList.length; p < plen; p++) {
          let reChArr = ' ';
          let chHost = channelList[p].title.url.split("/")[2].split("?")[0];
          let feedHref = feedItemDom.eq(i).attr("href");
          if (chHost == "video.sina.cn") {
            reChArr = "v.sina.cn";
          }
          if (feedHref) {
            // 看点特殊处理方式
            if(feedHref.indexOf("k.sina.cn") != -1 || feedHref.indexOf("kandian.sina.cn") != -1){
              let kandianCh = this.getUrlParam(feedHref,"from");
              let kandianSub = this.getUrlParam(feedHref,"subch");
              if(!kandianCh){
                // 栏目值匹配
                if(kandianSub){
                  let autoStr = 'nauto,bauto,uauto,cauto,iauto,oauto'.split(",");
                  let sportStr = 'cnfootball,global,nba,cba,tennis,run,golf,chess,f1,winter,horse,olympic,vollyball,pingpang,billiardball,fight,bodybuilding,gaming,osport'.split(",");
                  let entStr = 'star,film,tv,variety,music,krent,jpent,oent'.split(",");
                  let gameStr = 'comprehensivegame,onlinegame,mobilegame,esport,htmlgame,egame,vrgame,tvgame,ogame'.split(",");
                  kandianCh = 'news';
                  
                  autoStr.forEach(function(value){
                    if(value == kandianSub){
                      kandianCh = 'auto';
                    }
                  })
                  sportStr.forEach(function(value){
                    if(value == kandianSub){
                      kandianCh = 'sport';
                    }
                  })
                  entStr.forEach(function(value){
                    if(value == kandianSub){
                      kandianCh = 'ent';
                    }
                  })
                  gameStr.forEach(function(value){
                    if(value == kandianSub){
                      kandianCh = 'game';
                    }
                  })
                }else{
                  kandianCh = 'news'
                }
              }
              if(kandianCh){
                for (let k = 0, klen = channelList.length; k < plen; k++) {
                  if (channelList[k].title.url.indexOf(kandianCh) != -1) {
                    chArr = { name: channelList[k].title.title, link: channelList[k].title.url, subtitle: channelList[k].subtitle };
                    self.appendNav(chArr);
                    return false;
                  }
                }
                for (let k = 0, klen = channelList.length; k < plen; k++) {
                  if (channelList[k].title.url.indexOf("news") != -1) {
                    chArr = { name: channelList[k].title.title, link: channelList[k].title.url, subtitle: channelList[k].subtitle };
                    self.appendNav(chArr);
                    return false;
                  }
                }
              }else{
                for (let k = 0, klen = channelList.length; k < plen; k++) {
                  if (channelList[k].title.url.indexOf("news") != -1) {
                    chArr = { name: channelList[k].title.title,link: channelList[k].title.url, subtitle: channelList[k].subtitle };
                    self.appendNav(chArr);
                    return false;
                  }
                }
              }
            }
            if (feedHref.indexOf(chHost) != -1 || feedHref.indexOf(reChArr) != -1) {
              chArr = { name: channelList[p].title.title, link: channelList[p].title.url, subtitle: channelList[p].subtitle };
              self.appendNav(chArr);
              return false;
            }
          }
        }
      }
    }
  }
  getUrlParam(url = '',name) {
    const reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)')
    const urlStr = url.split("?")[1] || url.split("?")[0] || url;
    const value = urlStr.match(reg)
    return value === null ? null : decodeURI(value[2])
  }
  appendNav(navFixArr){
    let linkStr = '';
    let active = '';
    let hostName = navFixArr.link.split("//")[1].split('.')[0];
    linkStr += '<a href="' + navFixArr.link + '" data-sudaclick="' + hostName + '" class="fl_iq_nav_a active">' + navFixArr.name + '</a>';
    navFixArr.subtitle.forEach(function (nav, i) {
      if (i <= 3) {
        linkStr += '<a href="' + nav.url + '" data-sudaclick="' + hostName+i + '" class="fl_iq_nav_a ' + active + '">' + nav.title + '</a>';
      }
    }, this);
    if(this.fixBoxDom.html() == linkStr) return false;
    this.fixBoxDom.html(linkStr);
    // setTimeout(function(){
    //   this.fixBoxDom.removeClass("active")
    // }.bind(this),1000)
    this.scrollNav();
  }
}
function debounce(method, delay = 100){
  let timer = null
    return function() {
      const args = arguments
      clearTimeout(timer)

      timer = setTimeout(() => {
        method.apply(this, args)
      }, delay)
    }
}
const dynamicNavObj = new dynamicNav();
export default dynamicNavObj;



