/*jshint esversion:6*/

import newExposure from '@mfelibs/base-tools-exposure';

function initCardExpose() {
  var cardHandles = [{
      handle: '.h_module',
      tag: 'exp_title',
      recursive: false
    },
    {
      handle: '#j_top_nav',
      tag: 'exp_top_nav',
      recursive: false
    },
    {
      handle: '#j_imgSlide',
      tag: 'exp_imgSlide',
      recursive: false
    },
    {
      handle: '#j_news_scroll',
      tag: 'exp_news_scroll',
      recursive: false
    },
    {
      handle: '#j_card_yaowen',
      tag: 'exp_yaowen',
      recursive: false
    },
    {
      handle: '#j_card_breaking',
      tag: 'exp_breaking',
      recursive: false
    },

    // {handle: '#j_card_news', tag: 'exp_news', recursive: false},
    {
      handle: '#j_card_sports',
      tag: 'exp_sports',
      recursive: false
    },
    {
      handle: '#j_card_ent',
      tag: 'exp_ent',
      recursive: false
    },
    {
      handle: '#j_card_local',
      tag: 'exp_local',
      recursive: false
    },
    {
      handle: '#j_card_finance',
      tag: 'exp_finance',
      recursive: false
    },
    {
      handle: '#j_card_tech',
      tag: 'exp_tech',
      recursive: false
    },
    {
      handle: '#j_card_auto',
      tag: 'exp_auto',
      recursive: true
    },
    {
      handle: '#j_card_house',
      tag: 'exp_house',
      recursive: false
    },
    {
      handle: '#j_card_searchbar',
      tag: 'exp_searchbar',
      recursive: false
    },
    {
      handle: '#j_card_mil',
      tag: 'exp_mil',
      recursive: false
    },
    {
      handle: '#j_card_blog',
      tag: 'exp_blog',
      recursive: false
    },
    {
      handle: '#j_card_edu',
      tag: 'exp_edu',
      recursive: false
    },
    {
      handle: '#j_card_fashion',
      tag: 'exp_fashion',
      recursive: false
    },
    {
      handle: '#j_card_life',
      tag: 'exp_life',
      recursive: false
    },
    {
      handle: '#j_card_video',
      tag: 'exp_video',
      recursive: false
    },
    {
      handle: '#j_card_games',
      tag: 'exp_games',
      recursive: false
    },
    {
      handle: '#j_card_hotweibo',
      tag: 'exp_hotweibo',
      recursive: false
    },
    {
      handle: '#j_card_like_feed',
      tag: 'exp_like_feed',
      recursive: false
    },
    {
      handle: '#j_card_apprecommend',
      tag: 'exp_apprecommend',
      recursive: false
    },
    {
      handle: '#j_bottom_nav',
      tag: 'exp_bottom_nav',
      recursive: false
    },
    {
      handle: '#j_card_shehui',
      tag: 'exp_shehui',
      recursive: false
    }
  ];
  var i = 0;
  var j = 0;
  var len = cardHandles.length;
  var currentElement = null;
  var currentHandle = '';
  var recursionEles = [];
  var currentRec = null;
  var recLen = 0;

  for (i = 0; i < len; i++) {
    currentHandle = cardHandles[i].handle;

    currentElement = document.querySelector(currentHandle);

    currentElement && newExposure.pushListeningEle([currentElement], (function(index) {
      return function() {
        // console.log(index);

        var countInfo = {
          'type': 'cardViewExp',
          'title': 'cardViewExp',
          'name': cardHandles[index].tag,
          'index': 0
        };

        suda_count(countInfo);
      };
    })(i), false);

    //递归
    if (cardHandles[i].recursive) {
      recursionEles = currentElement.querySelectorAll('.f_card');

      for (j = 1, recLen = /*recursionEles.length*/ 4; j <= recLen; j++) {
        currentRec = recursionEles[j];

        currentRec && newExposure.pushListeningEle([currentRec], (function(i, j) {
          return function() {
            // console.log(index)

            var countInfo = {
              'type': 'cardViewExp',
              'title': 'cardViewExp',
              'name': cardHandles[i].tag,
              'index': j
            };

            suda_count(countInfo);
          };
        })(i, j), false);
      }
    }
  }
}

export default initCardExpose;



