const getCallupId = (url) =>{
	let info;
	//检查是否为comos新闻
    let domain = getDomain(url);
	info = getComosInfo(url, domain);

	if(info){
		return info;
	}
	
	//财经头条
    info = getCjTopInfo(url, domain);
    if (info)
    {
        return info;
    }

    //检查是否为博客
    info = getBlogInfo(url, domain);
    if(info){
        return info;
    }

    //检查是否为高清图
    info = getHdpicInfo(url, domain);
    if(info){
        return info;
    }

    //检查是否为媒体平台新闻
    info = getMpInfo(url, domain);
    if(info){
        return info;
    }

    return false;

    
}  
//获取媒体平台信息
    //http://mp.sina.com.cn/media_web/controllers/article.php?uid=1626398840&article_id=60f0e078001000004&fromsinago=1
const getMpInfo = (url, domain) => { 

    
    if(domain != 'kandian' && domain != 'k'){
        return false;
    }
    let arr = url.match('\/article\_([0-9a-zA-Z]+)_([0-9a-zA-Z]+)\.html');
    
    if(arr && arr[1] && arr[2]){
        
            let type_tag = arr[2].substring(0,1);
            /*
            if($type_tag=='p'){
                $mp_id = $matchs[1] . '-' . $matchs[2] . '-hdpic-mp';
            }else
            */
            if(type_tag=='m'){
                return arr[1] + '-' + arr[2] + '-video-mp';
            }else{
                return arr[1] + '-' + arr[2] + '-cms-mp';
            }
        
        
    }
    return false;
}
const getComosInfo = (url, domain) => {

    //从comos链接中截取出文档ID
    let arr = url.match('\-i([a-z]{7}[0-9]{7})');
    
    let docId = arr ? arr[1]:'';
    if (docId && domain){
        //原创正文模板
        let info = getOriginalArticleInfo(url,docId,domain);
        if (info)
        {
            return info;
        }
        //检查是否来自手浪“新浪头条”
        info = getConsiceArticleInfo(url,docId,domain);
        if (info)
        {
            return info;
        }
		
        if(domain == 'v'){
            domain="video";
        }
		 
        return docId+'-comos-'+domain+'-cms';
    }
    
}

// 获取原创正文模板信息
    // http://sinanews.sina.cn/2014-07-09/yc-ianfzhnw1618885.shtml?cID=6280
const getOriginalArticleInfo = (url,docId,domain) =>{
    
    if (domain != 'yd')
    {
        return false;
    }
    let mid = getAgt(url,'mid')||0;
    
    return docId + '-' + mid + '-comos-original';
    

}

 // 获取精编正文信息
    // http://top.sina.cn/fashion/2014-04-11/tnews-ianfzhne5912860.d.html
const getConsiceArticleInfo = (url, docId,domain) =>{

    if (domain != 'top')
    {
        return false;
    }
    
    return docId + '-comos-consice';
    
}
/*
     * 财经头条文章
     */
const getCjTopInfo = (url, domain) =>{

    if(domain=='cj'){
        let arr = url.match('\/article\/detail\/([0-9a-zA-Z]+)\/([0-9a-zA-Z]+)');
        
        if(arr && arr[1] && arr[2]){
            return arr[1]+'-'+arr[2]+'-cj-cjtop';
        }
    }
    return false;
}

//分析是否为博客信息
const getBlogInfo = (url, domain) =>{

    let nid = getBlogNid(url, domain);
    if(!nid){
        return false;
    }
    return nid+'-blog';
}
//获得blog的nid
const  getBlogNid = (url, domain) =>{

    if(domain != 'blog'){
        return false;
    }
    let arr = url.match('dpool\/blog\/s\/blog_(.*?).html');


    if(arr && arr[1]){
        return arr[1];
    }
    return false;
}

//获取高清图信息
const  getHdpicInfo = (url, domain) =>{

    if(domain != 'photo'){
        return false;
    }
    let arr = url.match('album_[0-9]_([0-9_]*)\.htm');
    if(arr && arr[1]){//新的高清图页静态化后的链接：http://photo.sina.cn/album_4_704_109720.html
  
        return arr[1].replace(/\_/g,'-')+'-hdpic';      
    }
 
    return false;
}

const getDomain = (url) =>{
    let arr =  url.match('\:\/\/([a-zA-Z0-9\.]{0,61})\.sina\.cn');
    return arr ? arr[1] : '';
}
const getAgt = (url, key) =>{
    if(url.indexOf(key)==-1){
        return '';
    }
    let str = url.split('?')[1];
    let arr1 = str.split('&');
    for(let i=0;i<arr1.length;i++){
        let arr2 = arr1[i].split('=');
        if(arr2[0]==key){
            return arr2[1];
        }
    }
    return '';
}
export default getCallupId;


